<template>
  <div class="page">
    <landing :section="section" />
    <section class="gallery">
      <figure v-for="project in section.projects" :key="project.name">
        <img :src="project.imgs[0]" />
        <button @click="gallery_shown = true">{{ project.name }}</button>
        <transition name="fade-back">
          <div class="all-images" v-if="gallery_shown">
            <img
              v-for="(img, index) in project.imgs"
              :src="img"
              :alt="`${project.name}(${index})`"
              :key="`${project.name}(${index})`"
            />
            <button @click="gallery_shown = false" class="close">
              <i class="fas fa-times fa-2x"></i>
            </button>
          </div>
        </transition>
      </figure>
      <h3 v-if="section.projects.length == 0">No Projects here yet</h3>
    </section>
    <f-footer />
  </div>
</template>

<script>
import landing from "../components/landing.vue";
import FFooter from "../components/FFooter.vue";

export default {
  name: "Social",
  components: { landing, FFooter },
  data() {
    return {
      gallery_shown: false,
      section: this.$store.state.sections.find(
        (section) => section.ename == "Search Engine Optimization"
      ),
    };
  },
};
</script>
